body{
    margin: 0;
    padding: 0;
    display: grid;
    place-items: center;
    background-image: linear-gradient(135deg, #FCE4EC,#213c65 );
    height: 100vh;
    font-family: cursive;
    }

    h1{
    text-align: center;
    color: blanchedalmond;
    font-size: 40px;
    }

    .container{
    background-color: #ada3af  ;
    border-radius: 10px;
    padding: 40px 40px 20px 40px;
    box-shadow: 0px 0px 10px  #bfb1b1;
    }

    ul{
    list-style: none;
    padding: 0;
    }

    #list{
    width: 450px;
    margin-bottom: 10px;
    }

    input, button{
    border: none;
    border-radius: 6px;
    padding: 10px;
    }

    input{
    width: 70%;
    margin-right: 10px;
    background-color: white;
    outline: none;
    font-family: unset;
    color: #6d0f7a;
    font-weight: 700;
    }

    button{
    color: #820b93;
    background-color: antiquewhite ;
    cursor: pointer;
    float: right;
    font-family: unset;
    font-weight: 700;
    }

    button.clear{
    color: #e4276c;
    float: none;
    display: block;
    margin: auto;
    }

    .task{
    display:flex;
    align-items: center;
    background-color: #f6f6f6;
    padding: 10px; 
    margin-bottom: 13px;
    border-radius: 6px; 
    position: relative;
    color: #6d0f7a;
    }

    span{
    padding: 5px; 
    font-size: 10px; 
    background-color: #d95c5c; 
    border-radius: 6px; 
    color: white;
    cursor: pointer; 
    position: absolute; 
    right: 10px;
    z-index: 2;
    }

    .checked{
    text-decoration: line-through;
    }

    .no{
    color: #444;
    background-color: #eee;
    text-align: center;
    }